<template>
    <el-container class="wrapper" :class="{'folded':folded}">
        <el-aside class="left" width="200px">
            <el-menu
                    :collapse="folded"
                    default-active="1"
                    background-color="#191a23"
                    text-color="#fff"
                    active-text-color="#2d8cf0"
            >
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span slot="title">同步系统</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="1" @click="openTab({name: '系统首页',icon: 'el-icon-s-home',path: '/sync/index'})">
                            <i class="el-icon-s-home"></i>
                            <span slot="title">系统首页</span>
                        </el-menu-item>
                        <el-menu-item index="2" @click="openTab({name: '同步计划',icon: 'el-icon-s-grid',path: '/sync/plan'})">
                            <i class="el-icon-s-grid"></i>
                            <span slot="title">同步计划</span>
                        </el-menu-item>
                        <el-menu-item index="3" @click="openTab({name: '同步记录',icon: 'el-icon-s-marketing',path: '/sync/result'})">
                            <i class="el-icon-s-marketing"></i>
                            <span slot="title">同步记录</span>
                        </el-menu-item>
                        <el-menu-item index="4" @click="openTab({name: '消息列表',icon: 'el-icon-tickets',path: '/sync/message'})">
                            <i class="el-icon-tickets"></i>
                            <span slot="title">消息列表</span>
                        </el-menu-item>
                        <el-menu-item index="5" @click="openTab({name: '回调列表',icon: 'el-icon-timer',path: '/sync/callback'})">
                            <i class="el-icon-timer"></i>
                            <span slot="title">回调列表</span>
                        </el-menu-item>
                        <el-menu-item index="6" @click="openTab({name: '系统配置',icon: 'el-icon-setting',path: '/sync/setting'})">
                            <i class="el-icon-setting"></i>
                            <span slot="title">系统配置</span>
                        </el-menu-item>
                    </el-menu-item-group>
                </el-submenu>


            </el-menu>
            <div class="folded-btn" @click="folded=!folded"><span
                    :class="[folded?'el-icon-s-unfold':'el-icon-s-fold']"></span></div>
        </el-aside>
        <el-container class="right">
            <el-header class="header">
            </el-header>
            <tabs-nav></tabs-nav>
        </el-container>
    </el-container>
</template>

<script>
    import store from '@/store/index'
    import tabsNav from "@/layouts/TabsNav";
    import router from '@/router/index'

    export default {
        components: {
            tabsNav
        },
        name: "Home.vue",
        data() {
            return {
                folded: false,
            }
        },
        methods: {
            openTab(item) {
                store.commit('addTabs', item);
                router.push(item.path);
            }
        },
    }
</script>

<style>
    .el-tabs__content {
        background-color: #ffffff;
        height: calc(100vh - 72px);
    }

    .header {
        /*background-color: #ffffff;*/
        /*box-shadow: 1px 1px 1px #999999;*/
        z-index: 1000;
        height: 0 !important;
    }

    .left {
        transition: all 200ms;
        /*border-right: 1px solid #eeeeee;*/
        background-color: #191a23;
    }

    .el-menu {
        border-right-width: 0 !important;
    }

    .main {
        background-color: #f8f8f9;
    }

    .wrapper {
        height: 100vh;
    }

    .folded-btn {
        background: #424449;
        color: #ffffff;
        text-align: center;
        position: fixed;
        bottom: 0;
        width: 200px;
        transition: all 200ms;
        cursor: pointer;
    }

    /** 折叠后的效果 */
    .folded .left {
        width: 60px !important;
    }

    .folded .el-menu span {
        display: none;
    }

    .folded .folded-btn {
        width: 60px
    }
</style>